import React from "react";
import { defineFeature, loadFeature } from "jest-cucumber";
import { Provider } from "react-redux";
import {
  BrowserRouter,
  Route,
  Routes,
  Navigate,
  MemoryRouter,
} from "react-router-dom";
import {
  act,
  cleanup,
  fireEvent,
  render,
  waitFor,
} from "@testing-library/react";
import "@testing-library/jest-dom/extend-expect";
import { store } from "../../Redux/store";
import routes from "./../../utils/Routes";
import Home from "../../Dictionary/Home";
import Details from "../../Dictionary/Details";
import axios from "axios";
const feature = loadFeature("src/Components/Features/details.feature");

defineFeature(feature, (test) => {
  test("User views word details with phonetics and meanings", ({
    given,
    when,
    then,
  }) => {
    given("The user is on the Details page", () => {
      const screen = render(
        <MemoryRouter initialEntries={["/details/example"]}>
          <Routes>
            <Route path="/details/:id" element={<Details />} />
          </Routes>
        </MemoryRouter>
      );
    }),
      when("The user views the details for a word", async () => {
        const screen = render(
          <MemoryRouter initialEntries={["/details/example"]}>
            <Routes>
              <Route path="/details/:id" element={<Details />} />
            </Routes>
          </MemoryRouter>
        );
        jest.spyOn(axios, "get").mockResolvedValueOnce({
          data: [
            {
              word: "example",
              phonetics: [
                {
                  text: "/ɪɡˈzæmpəl/",
                  audio: "example-audio.mp3",
                },
              ],
              meanings: [
                {
                  partOfSpeech: "noun",
                  definitions: [
                    {
                      definition: "a representative form or pattern",
                      example: "I followed your example",
                      synonyms: ["model", "pattern", "prototype"],
                    },
                  ],
                },
              ],
            },
          ],
        });
      });
    then("The word details should be displayed", () => {
      const screen = render(
        <MemoryRouter initialEntries={["/details/example"]}>
          <Routes>
            <Route path="/details/:id" element={<Details />} />
          </Routes>
        </MemoryRouter>
      );
      expect(screen.getByText("example")).toBeInTheDocument();
      expect(screen.getByText("/ɪɡˈzæmpəl/")).toBeInTheDocument();
      expect(
        screen.getByText("a representative form or pattern")
      ).toBeInTheDocument();
      expect(
        screen.getByText("Example: I followed your example")
      ).toBeInTheDocument();
      expect(
        screen.getByText("Synonyms: model, pattern, prototype")
      ).toBeInTheDocument();
    });
  });
});
